.home-conatiner {
  display: flex;
  flex-direction: column;
  gap: 4rem;
  justify-content: center;
  align-items: center;
}
img {
  width: 100%;
}
.home-conatiner h1,
.home-conatiner p {
  text-align: center;
  margin-bottom: 0%;
}
.designers_vision {
  display: flex;
  flex-direction: column;
  font-size: 54px;
  color: #7d64c4;
  margin-bottom: 10px;
  padding-bottom: 0%;
}

.changee-color {
  color: #000000;
  display: flex;
  flex-direction: column;
  font-size: 35px;
  margin-top: -70px;
  padding-top: -20px;
  font-weight: 100;
}
.school_vision p {
  flex-basis: 20%;
}
.events-div h2 {
  text-align: center;
}
.student_button {
  width: 20%;
  text-align: center;
  padding: 0.5rem;
  font-size: 1.3em;
  text-decoration: none;
  border-radius: 10px;
  background-color: rgb(34, 34, 110);
  color: white;
}
/* .student_button > span {
} */
.events {
  display: flex;
  gap: 1rem;
}
figure {
  display: flex;
  flex-flow: column nowrap;
  position: relative;
}
figcaption {
  text-align: center;
  background-color: #001219;
  color: white;
  padding: 1rem;
  position: absolute;
  bottom: 0%;
  width: 100%;
  opacity: 0.7;
}
figcaption:hover {
  opacity: 0.9;
}
.latestNews-div h2 {
  text-align: center;
}
.latestNews {
  display: flex;
  gap: 2rem;
}
